.transitionTable {
    margin: 10px 0 0 0;
  margin: 0 auto;
  border: 1px solid rgb(224, 224, 224);
  border-spacing: 0;
  border-radius: 5px;
  box-shadow: 0px 1px 6px rgba(46, 47, 66, 0.08),
    0px 1px 1px rgba(46, 47, 66, 0.16), 0px 2px 1px rgba(46, 47, 66, 0.08);
  overflow: hidden;

  & th,
  td {
    width: 150px;
    height: 30px;
    text-align: center;
  }

  & thead tr {
    background-color: tomato;
    color: white;
    text-transform: uppercase;
  }

  & tr {
    text-transform: capitalize;
  }

  & th:nth-of-type(3n + 1),
  td:nth-of-type(3n + 1),
  th:nth-of-type(3n + 2),
  td:nth-of-type(3n + 2) {
    border-right: 1px solid rgb(224, 224, 224);
  }
  & tr:nth-of-type(2n) {
    background-color: #eeeeee;
  }
}
.tableContainer {
    /* border: 1px solid rgb(224, 224, 224); */
    border-radius: 10px;
}
.tableTitle {
    padding: 10px;
    border: 1px solid rgb(224, 224, 224);
    border-radius: 10px;
}
